
/******
 *
 * Layout
 *
 *******/

 #content {
	 padding-top: 56px;
 }

 .page article.entry-content,
 .page header.entry-header,
 .research-section article.entry-content,
 .research-section header.entry-header {
	 max-width: 560px;
	 margin-left: auto;
     margin-right: auto;
 }
 .page .full-width {
   .entry-content {
     max-width: 100%;
   }
 }

 .index-layout {
	 max-width: 860px;
	 margin: 0 auto;
 }


.index-layout {
	.hentry {
		//max-width: 600px;
		//margin: 48px auto;
	}
	article.entry-content {
		margin-bottom: 3em;
	}
	margin-bottom: 4em;

  .post .entry-title {
    font-size: 1.5em;
    a { color: $gray; }
  }
}
.index-layout article {
  margin-bottom: 3em;
  figure {

  }
  header, .entry-summary {

  }
  .entry-title {
    margin: 0 0 0.25em;
  }
  .entry-meta {
    margin-bottom: 0.25em;
  }
}

img {
    max-width: 100%;
}

.page-header {
	margin-top: 0;
	padding: 2em 0;
	margin-bottom: 0;
    border-bottom: 0;
}
.single-research {
	.page-header {
		padding: 2em 0;

		.page-title {
            margin-top: -8px;
            margin-left: auto; margin-right: auto;
            max-width: 660px;
		}
	}
}
article.post {
	.entry-header {

		margin-bottom: 1em;
		.entry-title {
			margin-bottom: 0.75em;
		}
	}
	.entry-meta, .entry-meta a {
		color: #999;
	}
	.entry-meta > * {
		margin-right: 0.25em;
		padding-right: 0.5em;
		border-right: 2px solid #CCC;
	}
	.entry-meta > *:last-child {
		border: none;
	}
	.byline {
		display: inline-block;
	}
}
// Type adjustments
.entry-header{
	h1 {

		font-weight: 200;
	}
}
.page-layout {
	.entry-content {
    //margin-top: 3em;
  }
}

.section-header {
	.page-header {
		text-align: center;
		a {
            font-weight: 400;
		}
        p:last-child {
          margin-bottom: 0;
        }
	}
	.page-title {
        font-weight: 500;
		margin: 0 0 0.25em 0;
	}
}

.research-layout {
  @media screen and (min-width: $screen-md-max) {
    .entry-content { margin-left: 135px; padding-left: 15px; padding-right: 15px; }
  }
}


.post-post-content {
  margin-top: 4em;
  padding-left: 15px;
  padding-right: 15px;
}

#featured-content {
	padding-top: 2em;
	padding-bottom: 2em;
}
.featured-content {
  margin-top: 1em;
}
#about {
	padding-top: 4em;
	padding-bottom: 4em;
}

/* Headers */

.page-header {
  p { max-width: 570px; margin-left: auto; margin-right: auto; }
}

.research, .page.research, .single-research, .tax-research-categories {
	.page-header {
    background-image: none;
    // background: $brand-green-gradient;
    // color: rgba(0,0,0,0.69);
    border-bottom: 0;

    .byline {
      color: rgba(0,0,0,0.55);
    }
    a {
      color: rgba(0,0,0,0.55);
    }
  }
}

/******
 *
 * Typography
 *
 ******/

body {
	font-size: 16px;
    padding-bottom: 0;
    background: #FFF;
}

h1, h2, h3, h4, h5, h6 {
	line-height: 1.25;
}
h1 {
  font-size: 2.2em;
  font-weight: 500;
  //letter-spacing: -0.009em;
  line-height: 1.2;
}
h2 {
	font-size: 2em;
	font-weight: 600;
}
h3 {
	font-weight: 300;
}

h5 {
	text-transform: uppercase;
}
h6 {
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.1px;
}

.content {
  a {
    color: $brand-blue;
    font-weight: 600;
  }
  p a {
    color: $brand-primary;
  }
  h1 {
		margin-bottom: 0.25em;
	}
  h2 {

  }
  h3 {
    margin: 1em 0 0.5em;
    font-size: 1.25em;
    font-weight: 600;
    color: inherit;
  }
	h4 {
		margin-bottom: 0.5em;
	}
}

.headline {
  margin-bottom: .375em;
  margin-top: 0.25em;
  a { line-height: .9375; }
}

.widget-title {
	color: $gray;
	text-transform: uppercase;
}
.category, .entry-category {
  &, a {
    color: $brand-primary;
    font-weight: 400;
  }
}
.entry-title {
	font-weight: 600;
}
.page-title {
	margin: 0.25em 0 0 0;
}
.entry-content {
  a { font-weight: 400; }
}
.entry-content, .page-content, .entry-summary {
	p, ul, ol {
		font-family: 'abril-text', Georgia, serif;
		font-size: 16px;
		line-height: 1.625;
		margin-bottom: 1.125em;
	}
}
article.entry-content {

  section {
    padding-bottom: 2em;
    margin-bottom: 2em;

    .section-header {
      margin-bottom: 1.5em;
    }
  }
}
.category {
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Widgets */

.content .widget {
    margin-bottom: 3em;

	& > h3 {
        margin-bottom: 1em;
        color: $brand-gray;
    	font-weight: 400;
        font-size: 1em;
        text-transform: uppercase;
        letter-spacing: 1px;
        border-bottom: 1px solid $brand-gray-lighter;
    }

	ul {
		padding-left: 0;
		margin-bottom: 1.5em;

        li {
            list-style: none;
            margin-bottom: 1em;
        }
	}
}

.widget_sf_widget_constantcontact input[type="text"] {
    width: 100%;
}

.rpwe-block {
	h3 {
		background: none !important;
		clear: none;
		margin-bottom: 0 !important;
		margin-top: 0 !important;
		font-weight: 400;
		font-size: 14px !important;
		line-height: 1.25em;
	}
	.rpwe-thumb {
		height: 45px; width: 45px;
	}
}

.enp-list-posts {
    margin-bottom: 1.6rem;

    @extend .clearfix;
}

.enp-list-posts__title {
  display: block;
  margin-bottom: 0.5em;
}
.enp-list-posts___excerpt {
	display: block;
	font-family: 'abril-text', Georgia, serif;
	font-size: 16px;
	line-height: 1.375;
}
.crp_related_widget {

	li a {

		img {
			border: 1px solid #eee !important;
			box-shadow: none !important;
			margin: 2px 10px 2px 0;
			padding: 3px !important;

		}
	}
	li {
		margin-bottom: 0.5em;
	}
}
.enp-widget-row {

  .enp-list-posts {
    margin-left: -15px; margin-right: -15px;
    li {
      @include make-sm-column($sidebar-sm-columns);
    }
  }
}
.content .enp-widget-list.widget {
	padding: 1px 0 1px 2em;
	border-left: 1px solid #CCC;
	h3 {
		font-size: 16px;
		text-transform: uppercase;
		font-weight: 500;
	}
  a {
    font-weight: 400;
    font-size: 1em;
  }
  @media screen and (max-width: $screen-md-min) {
    padding: 1em 0 1px 0;
    border-left: 0;
    border-top: 1px solid #CCC;
  }
}
.widget_recent_entries {
	li > a {
		font-weight: 500;
		color: #555;
		margin-bottom: 4px;
		display: block;
		line-height: 1.5;
	}
	.post-date {
		display: block;
		color: #999;
	}
}
.enp-newsletter {
  // TODO add icon
}

.share-links {
  li {
    margin-bottom: .5em;
  }

  li a {
    font-weight: 400;
    color: lighten( $brand-gray, 12.5%);
  }
}
.widget h5 {
  color: lighten( $brand-gray, 25%);
  margin-bottom: 1em;
}

#featured-content {
	.widget ul {
		padding: 0;
		border-left: none;
	}
	.recent-posts-extended {
		a { color: $gray-light; }
		a:hover { text-decoration: underline; }
	}
}

.enp-author-list {
  list-style: none;
  padding-top: 1em;
  padding-left: 0;
  figure {
    float: left;
    img {
      border-radius: 50%;
      max-width: 91px;
      margin: 0 1em .5em 0;
    }
  }
  .author-content {
    margin-left: 121px;
    .entry-summary p {
      font-size: 14px;
      line-height: 1.5;
    }
  }
  .entry-header p {
    margin-bottom: 0.5em;
  }

  li {
  }
}

.enp-login {
  font-family: $font-family-base;
}

/* Home */

section.enp-related-research {
  padding: 1em 0 0;
  background-color: lighten($brand-gray-lightest, 7.5%);
}

section.enp-latest {

  figure {
  img {
    margin: 0 1em 1em 0;
    float: left;
    width: 25%;
    height: auto;
  }
  }
  time {
    font-size: 0.825em;
    color: $gray;
  }
  padding: 1em 0 1.5em;
}

#about {
	background-color: $brand-primary;
    background-image: url(../images/about_bg.jpg);
    @media (min-width: 768px) {
        padding-top: 6em;

        .headline {
            position: relative;
            top: -2rem;

            &:after {
                content: '';
                border-right: 1px solid rgba(255, 255, 255, 0.5);
                position: absolute;
                top: 0px;
                bottom: -20px;
                right: 0;
                transform: skewX(-30deg);
                z-index: 1;
            }
        }

        .col-sm-6 {
            margin-top: 6rem;
        }
    }

	&, a { color: #FFF; }
	.headline {
		margin-top: 0;
		font-weight: 100;
		color: #FFF;
	}
}


/* Research */

.enp-resources {
	padding: 1em;

	& > *:last-child {
		margin-bottom: 0;
	}
	.btn {
		width: 99%;
	}
	ul.research-links {
		margin-bottom: 0;
	}

    .btn-research-summary {
        display: block;
        margin-top: 2.5rem;
    }
}
a.btn {
	padding: 8px;
	& > img {
    vertical-align: middle;
    margin-right: 11px;
    margin-top: -5px;
	}
}
a.btn-research-report {
	border:none;
}



.research-category {
	padding: 3em 0 1em;
}

.research-section {
    margin-bottom: 3em;
	padding-bottom: 2em;
	padding-top: 2em;
    padding-left: 15px;
    padding-right: 15px;
    border-top: 1px solid $brand-gray-lighter;
    border-left: 1px solid $brand-gray-lighter;

	.research-category {
		text-align: center;
		margin-bottom: 1em;
	}
	.research-category-title {
		margin: 1em 0 2em;
	}
	.research-link {
		margin-bottom: 0;
	}
	.category-icon {
		display: inline-block;
		width: 64px;

		img { width: 100%; height: auto; }
	}
	article.entry-content {
		margin-bottom: 2em;
		& > p {
			padding-left: 36px;
		}
	}
	.entry-link, .entry-header, .entry-content {

	}
	& > p {

	}
}
.research-link {
    background-image: url(/wp-content/uploads/2015/12/ico_paper@2x.png);
	background-image: url(/wp-content/uploads/2015/12/ico_paper@2x.png);
	background-size: 18px 24px;
	background-repeat: no-repeat;
	background-position: left top;
	padding-left: 36px;
}

/* Quiz Creator Page */

article.quiz-creator-product-page {
  section {
    margin: 0;
  }
  .hero {
    padding-bottom: 0;
  }
  h3 {
    color: $brand-gray;
    margin: 2em 0 1em;
    font-weight: 500;
    font-size: 24px;
  }
}

.hero {
  .quiz-example {
      max-width: 480px;
      margin: 20px auto 0;
  }
}
.quiz-creator-intro-copy {
  padding: 4rem 0 2rem 6rem;
  @media (max-width: $screen-md) {
    padding: 4rem 0 2rem;
    text-align: center;
  }
}
section.quiz-creator-benefits {
  padding: 3rem 0;

  h4 {
    color: #333;
  }
  .row {
    padding-left: 30px;
    padding-right: 30px;
  }
  .emoji {
    font-size: 36px;
  }
}
section.quiz-creator-research {
  padding-top: 4em;
  padding-bottom: 4em;
  position: relative;
  border: 1px solid $brand-yellow;

  &:after {
      content: '';
      background: $brand-gray-lightest;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      transform: skewY(-8deg);
      z-index: -1;
  }

  .headline {
    font-weight: 200;
  }
  .subhead {
    color: $brand-blue;
  }

}
.quiz-creator-steps {
  .step-header {
    color: $brand-blue;
  }
}
.inner-wrapper {
  max-width: 760px;
  margin-left: auto;
  margin-right: auto;
}

.row-spacing-sm {

}
.row-spacing-md {
  padding-top: 2em;
  padding-bottom: 2em;
}
.row-spacing-lg {

}
/* Images */
.featured-image {
  margin-bottom: 1em;
}
.wp-post-image {
	max-width: 100%;
  height: auto;
}

/* Twitter */

.TwitterTweets h3 {
    color: $brand-gray;
    font-weight: 400;
    font-size: 1em;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.fetched_tweets {


	//box-align: stretch;
	//align-content: middle;
	padding: 0;
	margin-left: -15px;
	margin-right: -15px;
	margin-bottom: 8em;
	li {
		display: inline-block;
		width: 33%;
		list-style: none;
		border: none;
		border-right: 1px solid #CCC;
		padding: 15px;
		vertical-align: top;
		&:last-child {
			border-right: none;
		}
  }

	.tweet_data {
		font-family: 'abril-text', Georgia, serif;
	}
	.times {
		a { color: $gray-light; }
	}
}
@media screen and (max-width: $screen-sm-min) {
  .fetched_tweets li { width: 100%; display: block; border: 0; border-bottom: $gray-light; }
}
footer {
	.widget {

	}
}


/******
 *
 * Misc.
 *
 ******/

 .wf-loading {
	 h1, h2, h3, h4, h5, h6, p, time, figcaption, span {
		 visibility: hidden;
	 }
 }

 .visual-form-builder fieldset {
	 clear: none;
 }

 .media {
	 margin: 0;
 }
 .research-menu {
   margin-top: 2em;
   a {

   }
 }
 .research-menu a.icon-link {
   padding-left: 2.5em;
   img {
     float: left;
     width: auto;
     height: 30px;
     margin-left: -40px;
     margin-top: -4px;
   }
 }

 .pre {
  font: 12px/14px Monospace;
  border: 1px solid #CCC;
  height: auto;
  background: #FFFFE0;
  border-radius: 3px;
  padding: 5px;

 }

 /******
  *
  * Figure Styles
  *
  ******/

figure {
  margin-bottom: 1em;

  .title, p {
    color: $brand-gray;
  }
  p {
    font-size: 13px;
  }
  figcaption {
    font-size: 11px;
    color: $brand-gray-light;
  }
}
figure.wide {
  @media (min-width: 920px) {
    width: 760px;
  }
}

 /******
  *
  * Table Styles
  *
  ******/

table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
}
th, td {

}
th {

}
td {

}

/* Data Table Styles */

.enp-data-table {
  margin: 2em 0;
  padding-top: 0.125em;
  border-top: 1px solid #CCC;
}

table {
  border: 1px solid $brand-gray-lighter;
  font-size: 13px;
  margin-bottom: 5px;
}
th {
  font-family: $font-family-headlines;
  font-size: 16px;

  border-bottom: 1px solid $brand-gray-lighter;
  background-color: $brand-gray-lightest;
  small {
    font-weight: 300;
  }
}
thead th {
  font-weight: 500;
}
tbody th {
  background-color: lighten($brand-gray-lightest,7.5%);
}
th, td {
  padding: 0.75em 1em;
  line-height: 1.25;
  border-left: 1px solid $brand-gray-lighter;
}
td {
  border-bottom: 1px solid $brand-gray-lighter;
}


/* Footnotes */

.footnotes li {
    font-family: $font-family-base;
    font-size: 11px;
    margin-bottom: 9px;
    line-height: 1.75;
    color: #666;
}

.footnotes ol {
    padding-left: 15px;
}

.footnotes {
    border-top: 1px solid #CCC;
    padding-top: 2em;
}

iframe {
    border: none;
}
